html,
body {
  -webkit-text-size-adjust: 100%;
}
body {
  background-image: url("../img/Amiibo Background Left edit-400.jpg"), url("../img/Amiibo Background Right edit-400.jpg");
  background-size: 400px auto;
  background-attachment: fixed;
  background-repeat: repeat-y;
  background-position: left, right;
  color: #888;
  font-family: Helvetica, Arial, sans-serif;
  margin: 0;
  padding: 0;
}
@media screen and (min-width: 1300px) {
  body {
    background-image: url("../img/Amiibo Background Left edit-400.jpg"), url("../img/Amiibo Background Right edit-400.jpg");
    background-size: 400px auto;
  }
}
@media screen and (min-width: 1200px) and (max-width: 1300px) {
  body {
    background-image: url("../img/Amiibo Background Left edit-300.jpg"), url("../img/Amiibo Background Right edit-300.jpg");
    background-size: 300px auto;
  }
}
@media screen and (min-width: 1000px) and (max-width: 1200px) {
  body {
    background-image: url("../img/Amiibo Background Left edit-250.jpg"), url("../img/Amiibo Background Right edit-250.jpg");
    background-size: 250px auto;
  }
}
@media screen and (min-width: 900px) and (max-width: 1000px) {
  body {
    background-image: url("../img/Amiibo Background Left edit-200.jpg"), url("../img/Amiibo Background Right edit-200.jpg");
    background-size: 200px auto;
  }
}
@media screen and (max-width: 900px) {
  body {
    background-image: url("../img/Amiibo Background Left edit-150.jpg"), url("../img/Amiibo Background Right edit-150.jpg");
    background-size: 150px auto;
  }
}
#container {
  margin-left: auto;
  margin-right: auto;
  width: 60%;
  float: center;
}
#banner {
  background-image: linear-gradient(45deg, #ff5555, #ffffff);
  height: 200px;
}
#banner .image {
  width: 50%;
  height: 100%;
  background-repeat: no-repeat;
  background-size: contain;
}
#banner .image.left {
  background-image: url("../img/Super Smash Bros Banner cropped-200.png");
  background-position: center left;
  float: left;
}
#banner .image.right {
  background-image: url("../img/Super Smash Bros Logo-200.png");
  background-position: center right;
  float: right;
}
@media screen and (min-width: 1401px) {
  #banner {
    height: 200px;
  }
  #banner.left {
    background-image: url("../img/Super Smash Bros Banner cropped-200.png");
  }
  #banner.right {
    background-image: url("../img/Super Smash Bros Logo-200.png");
  }
}
@media screen and (min-width: 1201px) and (max-width: 1400px) {
  #banner {
    height: 175px;
  }
  #banner.left {
    background-image: url("../img/Super Smash Bros Banner cropped-175.png");
  }
  #banner.right {
    background-image: url("../img/Super Smash Bros Logo-175.png");
  }
}
@media screen and (min-width: 851px) and (max-width: 1200px) {
  #banner {
    height: 150px;
  }
  #banner.left {
    background-image: url("../img/Super Smash Bros Banner cropped-150.png");
  }
  #banner.right {
    background-image: url("../img/Super Smash Bros Logo-150.png");
  }
}
@media screen and (min-width: 851px) {
  #container {
    width: 700px;
  }
}
@media screen and (min-width: 451px) and (max-width: 850px) {
  #banner {
    height: 100px;
  }
  #banner.left {
    background-image: url("../img/Super Smash Bros Banner cropped-100.png");
  }
  #banner.right {
    background-image: url("../img/Super Smash Bros Logo-100.png");
  }
  #container {
    width: 80%;
  }
}
@media screen and (min-width: 0) and (max-width: 450px) {
  #banner {
    height: 75px;
  }
  #banner.left {
    background-image: url("../img/Super Smash Bros Banner cropped-75.png");
  }
  #banner.right {
    background-image: url("../img/Super Smash Bros Logo-75.png");
  }
  body {
    background-image: none;
  }
  #container {
    width: 100%;
  }
}
#toolbar {
  background-color: #ff0000;
  color: #FFF;
  padding-left: 5;
}
#content {
  padding: 5;
  margin-left: auto;
  margin-right: auto;
  background-color: #ffffff;
}
#footer {
  clear: both;
  bottom: 0;
  background-color: #FFF;
  padding-left: 5;
  padding-bottom: 2;
  font-size: 15px;
}
