@header: #f00;
@left-grad: @header + #555;
@right-grad: #fff;
@image-location: "../img";
@background-left: "@{image-location}/Amiibo Background Left edit-#.jpg";
@background-right: "@{image-location}/Amiibo Background Right edit-#.jpg";
@header-left: "@{image-location}/Super Smash Bros Banner cropped-#.png";
@header-right: "@{image-location}/Super Smash Bros Logo-#.png";
html, body {-webkit-text-size-adjust: 100%;}

.background-image-adder(@image-name, @size) {
    @image: replace(@image-name, "#", @size);
    background-image+: url(@image);
}

body {
    @size: 400;
    .background-image-adder(@background-left, @size);
    .background-image-adder(@background-right, @size);
    background-size: unit(@size, px) auto;
    @media screen and (min-width: 1300px) {
        @size: 400;
        .background-image-adder(@background-left, @size);
        .background-image-adder(@background-right, @size);
        background-size: unit(@size, px) auto;
    }
    @media screen and (min-width: 1200px) and (max-width: 1300px){
        @size: 300;
        .background-image-adder(@background-left, @size);
        .background-image-adder(@background-right, @size);
        background-size: unit(@size, px) auto;
    }
    @media screen and (min-width: 1000px) and (max-width: 1200px) {
        @size: 250;
        .background-image-adder(@background-left, @size);
        .background-image-adder(@background-right, @size);
        background-size: unit(@size, px) auto;
    }
    @media screen and (min-width: 900px) and (max-width: 1000px) {
        @size: 200;
        .background-image-adder(@background-left, @size);
        .background-image-adder(@background-right, @size);
        background-size: unit(@size, px) auto;
    }
    @media screen and (max-width: 900px) {
        @size: 150;
        .background-image-adder(@background-left, @size);
        .background-image-adder(@background-right, @size);
        background-size: unit(@size, px) auto;
    }
    // background-size: contain;
    background-attachment: fixed;
    background-repeat: repeat-y;
    background-position: left, right;
    color: #888;
    font-family: Helvetica, Arial, sans-serif;
    margin: 0;
    padding: 0;
}
#container {
    margin-left: auto;
    margin-right: auto;
    width: 60%;
    float: center;
}

#banner {
    background-image: linear-gradient(45deg, @left-grad, @right-grad);
    @size: 200;
    height: unit(@size, px);
    .image {
        width: 50%;
        height: 100%;
        background-repeat: no-repeat;
        background-size: contain;
        &.left {
            .background-image-adder(@header-left, @size);
            background-position: center left;
            float: left
        }
        &.right {
            .background-image-adder(@header-right, @size);
            background-position: center right;
            float: right;
            // display: inline-block;
        }
    }
}
@media screen and (min-width: 1401px) {
    #banner {
        @size: 200;
        height: unit(@size, px);
        &.left {
            .background-image-adder(@header-left, @size);
        }
        &.right {
            .background-image-adder(@header-right, @size);
        }
    }
}
@media screen and (min-width: 1201px) and (max-width: 1400px) {
    #banner {
        @size: 175;
        height: unit(@size, px);
        &.left {
            .background-image-adder(@header-left, @size);
        }
        &.right {
            .background-image-adder(@header-right, @size);
        }
    }
}
@media screen and (min-width: 851px) and (max-width: 1200px) {
    #banner {
        @size: 150;
        height: unit(@size, px);
        &.left {
            .background-image-adder(@header-left, @size);
        }
        &.right {
            .background-image-adder(@header-right, @size);
        }
    }
}
@media screen and (min-width: 851px) {
    #container {
        width: 700px;
    }
}
@media screen and (min-width: 451px) and (max-width: 850px) {
    #banner {
        @size: 100;
        height: unit(@size, px);
        &.left {
            .background-image-adder(@header-left, @size);
        }
        &.right {
            .background-image-adder(@header-right, @size);
        }
    }
    #container {
        width: 80%;
    }
}
@media screen and (min-width: 0) and (max-width: 450px) {
    #banner {
        @size: 75;
        height: unit(@size, px);
        &.left {
            .background-image-adder(@header-left, @size);
        }
        &.right {
            .background-image-adder(@header-right, @size);
        }
    }
    body {
        background-image: none;
    }
    #container {
        width: 100%;
    }
}
#toolbar {
    background-color: @header;
    color: #FFF;
    padding-left: 5
}

#content {
    padding: 5;
    margin-left: auto;
    margin-right: auto;
    background-color: #FFF
}

#footer {
    clear: both;
    bottom: 0;
    background-color: #FFF;
    padding-left: 5;
    padding-bottom: 2;
    font-size: 15px
}
